import { Component, Input, OnChanges, OnInit, SimpleChanges, ViewChild } from '@angular/core';
import { iif, Observable, of } from 'rxjs';
import {
  IRecordGridColumn,
  IRecordGridConfig,
  IRecordGridDataPageParams,
  RecordGridComponent
} from '@helix/platform/view/components';
import { IDataPageResult, RxDataPageFactoryService } from '@helix/platform/shared/api';
import { TranslateService } from '@ngx-translate/core';
import { RX_APPROVAL_CONSOLE } from '../approval-console.constant';
import { IRecordDefinition, RX_RECORD_DEFINITION, RxRecordInstanceService } from '@helix/platform/record/api';
import { noop, omit } from 'lodash';
import { ApprovalGridType, IApprovalRequest, INeedAttentionRequest } from '../approval-console.types';
import { IRowDataItem } from '@helix/platform/view/api';
import { AdaptModalService } from '@bmc-ux/adapt-angular';
import { ApprovalRequestCommentComponent } from '../approval-request-comment/approval-request-comment.component';
import { switchMap, tap } from 'rxjs/operators';
import { RxApprovalConsoleService } from '../approval-console.service';
import { ReadOnlyFieldsModalComponent, RxModalService } from '@helix/platform/ui-kit';
import { RxApprovalQCIDataPageService } from '../approval-qci-data-page.service';

@Component({
  selector: 'rx-approval-request-comments',
  templateUrl: './approval-request-comments.component.html'
})
export class ApprovalRequestCommentsComponent implements OnInit, OnChanges {
  @Input()
  approvalRequest: IApprovalRequest | INeedAttentionRequest;

  @Input()
  gridType: ApprovalGridType;

  @ViewChild('requestCommentsGrid')
  requestCommentsGrid: RecordGridComponent;

  gridConfig$: Observable<IRecordGridConfig>;
  private selectedRequest: IApprovalRequest;

  constructor(
    private rxApprovalQCIDataPageService: RxApprovalQCIDataPageService,
    private rxModalService: RxModalService,
    private rxApprovalConsoleService: RxApprovalConsoleService,
    private adaptModalService: AdaptModalService,
    private rxDataPageService: RxDataPageFactoryService,
    private rxRecordInstanceService: RxRecordInstanceService,
    private translateService: TranslateService
  ) {}

  ngOnInit(): void {
    this.gridConfig$ = of({
      enableRowSelection: null,
      getData: (queryParams: IRecordGridDataPageParams) => this.getCommentData(queryParams),
      columns: this.getCommentColumns(),
      getRecordDefinition: () => of(this.getCommentRecordDefinition()),
      recordDefinitionName: RX_APPROVAL_CONSOLE.requestDetailsGrid.definition,
      styles: 'flex-fill'
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.approvalRequest?.currentValue) {
      this.requestCommentsGrid?.api.refresh().subscribe();
    }
  }

  addNewComment(): void {
    this.rxModalService
      .openModal({
        content: ApprovalRequestCommentComponent,
        size: 'sm',
        data: {
          selectedRequest: this.selectedRequest
        }
      })
      .then((result) => {
        if (result) {
          this.requestCommentsGrid.api.refresh().subscribe();
        }
      })
      .catch(noop);
  }

  private getCommentData(queryParams: IRecordGridDataPageParams): Observable<IDataPageResult> {
    return iif(
      () => this.gridType === ApprovalGridType.NeedAttentionRequests,
      this.rxApprovalConsoleService.getCurrentRequestDetails(
        this.approvalRequest[RX_APPROVAL_CONSOLE.needAttentionRequestsGrid.fields.requestID],
        this.approvalRequest[RX_APPROVAL_CONSOLE.needAttentionRequestsGrid.fields.application]
      ),
      of(this.approvalRequest as IApprovalRequest)
    ).pipe(
      tap((currentRequest: IApprovalRequest) => {
        this.selectedRequest = currentRequest;
      }),
      switchMap((currentRequest: IApprovalRequest) =>
        this.rxApprovalQCIDataPageService.get({
          params: omit(
            {
              ...queryParams,
              shouldIncludeTotalSize: true,
              [RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.application]: currentRequest.application,
              [RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.signatureID]: currentRequest.signatureID,
              [RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.type]:
                RX_APPROVAL_CONSOLE.requestDetailsGrid.commentTypeValue,
              [RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.request]: currentRequest.request
            },
            ['searchText']
          )
        })
      )
    );
  }

  private getCommentColumns(): IRecordGridColumn[] {
    return [
      {
        index: 1,
        fieldId: RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.from.toString(),
        title: this.translateService.instant('com.bmc.arsys.rx.client.approval.console.submitter.label')
      },
      {
        index: 2,
        fieldId: RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.response.toString(),
        title: this.translateService.instant('com.bmc.arsys.rx.client.common.comment.label'),
        clickable: true,
        actions: [
          {
            name: (previousAction: any, selectedRow: IRowDataItem) => {
              this.showCommentDetails(selectedRow);
            }
          }
        ],
        sortable: false
      },
      {
        index: 3,
        fieldId: RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.attachment.toString(),
        title: this.translateService.instant('com.bmc.arsys.rx.client.common.attachment.label'),
        clickable: true,
        actions: [
          {
            name: (previousAction: any, selectedRow: IRowDataItem) => {
              this.rxRecordInstanceService.downloadAttachment(
                RX_APPROVAL_CONSOLE.requestDetailsGrid.questionDefinition,
                Number(RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.attachment),
                selectedRow[RX_RECORD_DEFINITION.coreFieldIds.id].split('|')[0],
                selectedRow[RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.attachment]
              );
            }
          }
        ]
      },
      {
        index: 4,
        fieldId: RX_RECORD_DEFINITION.coreFieldIds.id.toString(),
        title: this.translateService.instant('com.bmc.arsys.rx.client.common.id.label'),
        visible: false,
        filterable: false
      }
    ];
  }

  private getCommentRecordDefinition(): IRecordDefinition {
    return {
      fieldDefinitions: [
        {
          id: RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.from,
          resourceType: RX_RECORD_DEFINITION.resourceTypes.character
        },
        {
          id: RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.response,
          resourceType: RX_RECORD_DEFINITION.resourceTypes.character
        },
        {
          id: RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.attachment,
          resourceType: RX_RECORD_DEFINITION.resourceTypes.attachment
        },
        {
          id: RX_RECORD_DEFINITION.coreFieldIds.id,
          resourceType: RX_RECORD_DEFINITION.resourceTypes.character
        }
      ]
    };
  }

  private showCommentDetails(selectedRow: IRowDataItem) {
    this.adaptModalService
      .open({
        title: this.translateService.instant('com.bmc.arsys.rx.client.common.comment.label'),
        content: ReadOnlyFieldsModalComponent,
        data: {
          fields: [
            {
              label: this.translateService.instant('com.bmc.arsys.rx.client.approval.console.submitter.label'),
              value: selectedRow[RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.from]
            },
            {
              label: this.translateService.instant('com.bmc.arsys.rx.client.common.comment.label'),
              value: selectedRow[RX_APPROVAL_CONSOLE.requestDetailsGrid.fields.response]
            }
          ]
        },
        size: 'sm'
      })
      .catch(noop);
  }
}
